'use client'

import { motion } from 'framer-motion'
import { MapPin, Plane, Sun, Waves, Building2, TrendingUp } from 'lucide-react'

const whyCancun = [
  {
    icon: Sun,
    title: 'Clima ideal',
    description: '300+ días de sol al año. Temperatura promedio 26°C perfecta para contrast therapy.',
  },
  {
    icon: Waves,
    title: 'Mar Caribe',
    description: 'Acceso a playas vírgenes y cenotes sagrados mayas a minutos del club.',
  },
  {
    icon: Plane,
    title: 'Conectividad global',
    description: 'Aeropuerto internacional con vuelos directos desde 70+ ciudades del mundo.',
  },
  {
    icon: Building2,
    title: 'Hub wellness',
    description: 'Epicentro del turismo wellness en Latinoamérica con crecimiento del 25% anual.',
  },
]

const expansionPlans = [
  { city: 'Tulum', status: 'Q1 2027', phase: 'En planeación' },
  { city: 'Ciudad de México', status: 'Q3 2027', phase: 'Investigación' },
  { city: 'Monterrey', status: 'Q1 2028', phase: 'Futuro' },
]

export default function Location() {
  return (
    <section className="py-24 md:py-32 px-4 bg-white" id="location">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ duration: 0.8 }}
          className="text-center mb-16"
        >
          <div className="inline-flex items-center gap-2 mb-4">
            <MapPin className="w-6 h-6 text-aura-gold" />
            <span className="text-aura-gold font-semibold uppercase tracking-wider text-sm">
              Ubicación
            </span>
          </div>
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-serif font-light text-aura-black mb-6">
            Cancún,{' '}
            <span className="text-aura-gold font-medium italic">México</span>
          </h2>
          <p className="text-xl md:text-2xl text-aura-black/70 max-w-3xl mx-auto">
            En el corazón del Caribe Mexicano, donde el wellness se encuentra con el paraíso
          </p>
        </motion.div>

        {/* Map */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.2, duration: 0.6 }}
          className="mb-20"
        >
          <div className="relative rounded-3xl overflow-hidden h-[400px] md:h-[600px] bg-gradient-to-br from-aura-jade/20 to-aura-terracota/20">
            {/* Map Placeholder */}
            <div className="absolute inset-0 flex items-center justify-center">
              <div className="text-center">
                <MapPin className="w-20 h-20 text-aura-gold/50 mx-auto mb-4" />
                <p className="text-aura-black/50 text-lg">
                  Mapa interactivo de Cancún
                </p>
                <p className="text-aura-black/30 text-sm mt-2">
                  Ubicación exacta se revelará a miembros del waitlist en Q1 2026
                </p>
              </div>
            </div>

            {/* Location Badge */}
            <div className="absolute top-8 left-8 glass px-6 py-3 rounded-full">
              <p className="text-aura-black font-semibold">
                📍 Zona Hotelera, Cancún, Quintana Roo
              </p>
            </div>

            {/* Quick Facts */}
            <div className="absolute bottom-8 right-8 bg-white/90 backdrop-blur-sm rounded-2xl p-6 max-w-xs">
              <h4 className="font-serif font-medium text-aura-black mb-3">
                Acceso fácil
              </h4>
              <div className="space-y-2 text-sm text-aura-black/70">
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-1.5 rounded-full bg-aura-gold" />
                  <span>15 min del aeropuerto</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-1.5 rounded-full bg-aura-gold" />
                  <span>5 min de la playa</span>
                </div>
                <div className="flex items-center gap-2">
                  <div className="w-1.5 h-1.5 rounded-full bg-aura-gold" />
                  <span>Parking gratuito</span>
                </div>
              </div>
            </div>
          </div>
        </motion.div>

        {/* Why Cancún */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.3, duration: 0.6 }}
          className="mb-20"
        >
          <h3 className="text-3xl md:text-4xl font-serif font-light text-aura-black text-center mb-12">
            ¿Por qué Cancún?
          </h3>

          <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
            {whyCancun.map((reason, index) => {
              const Icon = reason.icon
              return (
                <motion.div
                  key={index}
                  initial={{ opacity: 0, y: 20 }}
                  whileInView={{ opacity: 1, y: 0 }}
                  viewport={{ once: true }}
                  transition={{ delay: 0.1 * index, duration: 0.5 }}
                  className="text-center"
                >
                  <div className="inline-flex items-center justify-center w-16 h-16 rounded-full bg-aura-gold/10 mb-4">
                    <Icon className="w-8 h-8 text-aura-gold" strokeWidth={1.5} />
                  </div>
                  <h4 className="text-xl font-serif font-medium text-aura-black mb-3">
                    {reason.title}
                  </h4>
                  <p className="text-aura-black/70 leading-relaxed">
                    {reason.description}
                  </p>
                </motion.div>
              )
            })}
          </div>
        </motion.div>

        {/* Expansion Plans */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.4, duration: 0.6 }}
          className="bg-gradient-to-br from-bg-secondary to-white rounded-3xl p-8 md:p-12"
        >
          <div className="flex items-center gap-3 mb-8">
            <TrendingUp className="w-6 h-6 text-aura-gold" />
            <h3 className="text-2xl md:text-3xl font-serif font-medium text-aura-black">
              Planes de expansión
            </h3>
          </div>

          <p className="text-aura-black/70 text-lg mb-8 max-w-2xl">
            Cancún es solo el comienzo. Nuestra visión es crear una red de social wellness clubs en las ciudades más vibrantes de México.
          </p>

          <div className="grid md:grid-cols-3 gap-6">
            {expansionPlans.map((plan, index) => (
              <div
                key={index}
                className="bg-white rounded-2xl p-6 border border-aura-black/5"
              >
                <div className="flex items-center justify-between mb-4">
                  <h4 className="text-xl font-serif font-medium text-aura-black">
                    {plan.city}
                  </h4>
                  <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                    plan.phase === 'En planeación' ? 'bg-aura-gold/20 text-aura-gold' :
                    plan.phase === 'Investigación' ? 'bg-aura-jade/20 text-aura-jade' :
                    'bg-aura-black/10 text-aura-black/50'
                  }`}>
                    {plan.phase}
                  </span>
                </div>
                <p className="text-aura-black/70">
                  Apertura estimada: <span className="font-semibold text-aura-gold">{plan.status}</span>
                </p>
              </div>
            ))}
          </div>

          <div className="mt-8 text-center">
            <p className="text-aura-black/60 text-sm">
              ¿Tu ciudad no está en la lista?{' '}
              <a href="#waitlist" className="text-aura-gold font-semibold hover:underline">
                Únete al waitlist
              </a>
              {' '}y cuéntanos dónde te gustaría ver NUMA.
            </p>
          </div>
        </motion.div>

        {/* Distance Highlights */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.5, duration: 0.6 }}
          className="mt-16 grid md:grid-cols-3 gap-8 text-center"
        >
          <div>
            <div className="text-4xl font-serif font-light text-aura-gold mb-2">
              15 min
            </div>
            <p className="text-aura-black/70">
              Aeropuerto Internacional de Cancún
            </p>
          </div>
          <div>
            <div className="text-4xl font-serif font-light text-aura-gold mb-2">
              5 min
            </div>
            <p className="text-aura-black/70">
              Playas del Caribe Mexicano
            </p>
          </div>
          <div>
            <div className="text-4xl font-serif font-light text-aura-gold mb-2">
              30 min
            </div>
            <p className="text-aura-black/70">
              Cenotes sagrados mayas
            </p>
          </div>
        </motion.div>
      </div>
    </section>
  )
}
