'use client'

import { motion } from 'framer-motion'
import { Flame, Users, Sparkles } from 'lucide-react'

const pillars = [
  {
    icon: Flame,
    title: 'Contrast Therapy',
    subtitle: 'Sauna + Hielo',
    description: 'Transforma tu cuerpo a través del poder del contraste térmico. Sauna infrarrojo, ice baths y breathwork guiado.',
    color: 'aura-terracota',
  },
  {
    icon: Users,
    title: 'Social Wellness',
    subtitle: 'Conexión Humana',
    description: 'No es un spa, es una comunidad. Experiencias compartidas que crean vínculos auténticos y duraderos.',
    color: 'aura-gold',
  },
  {
    icon: Sparkles,
    title: 'Cultural Heritage',
    subtitle: 'Medicina Ancestral',
    description: 'Temazcal moderno, sound healing y prácticas ancestrales mexicanas adaptadas al biohacking contemporáneo.',
    color: 'aura-jade',
  },
]

const containerVariants = {
  hidden: { opacity: 0 },
  visible: {
    opacity: 1,
    transition: {
      staggerChildren: 0.2,
      delayChildren: 0.3,
    },
  },
}

const itemVariants = {
  hidden: { opacity: 0, y: 30 },
  visible: {
    opacity: 1,
    y: 0,
    transition: {
      duration: 0.8,
      ease: [0.16, 1, 0.3, 1] as [number, number, number, number],
    },
  },
}

export default function Positioning() {
  return (
    <section className="py-24 md:py-32 px-4 bg-bg-secondary" id="positioning">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ duration: 0.8 }}
          className="text-center mb-20"
        >
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-serif font-light text-aura-black mb-6">
            ¿Qué es{' '}
            <span className="text-aura-gold font-medium italic">NUMA</span>?
          </h2>
          <p className="text-xl md:text-2xl text-aura-black/70 max-w-3xl mx-auto">
            Tres pilares que transforman tu bienestar físico, mental y social
          </p>
        </motion.div>

        {/* 3-Column Grid */}
        <motion.div
          variants={containerVariants}
          initial="hidden"
          whileInView="visible"
          viewport={{ once: true, margin: "-100px" }}
          className="grid md:grid-cols-3 gap-8 mb-16"
        >
          {pillars.map((pillar, index) => {
            const Icon = pillar.icon
            return (
              <motion.div
                key={index}
                variants={itemVariants}
                className="card-hover bg-white rounded-2xl p-8 md:p-10 border border-aura-black/5 hover:border-aura-gold/30"
              >
                <div className={`inline-flex items-center justify-center w-16 h-16 rounded-full bg-${pillar.color}/10 mb-6`}>
                  <Icon className={`w-8 h-8 text-${pillar.color}`} strokeWidth={1.5} />
                </div>
                <h3 className="text-2xl md:text-3xl font-serif font-medium text-aura-black mb-2">
                  {pillar.title}
                </h3>
                <p className="text-aura-gold font-semibold mb-4 text-lg">
                  {pillar.subtitle}
                </p>
                <p className="text-aura-black/70 leading-relaxed">
                  {pillar.description}
                </p>
              </motion.div>
            )
          })}
        </motion.div>

        {/* Video Testimonial / B-roll Placeholder */}
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ delay: 0.6, duration: 0.8 }}
          className="relative rounded-3xl overflow-hidden h-[400px] md:h-[600px] bg-gradient-to-br from-aura-obsidiana to-aura-jade"
        >
          {/* Video placeholder */}
          <div className="absolute inset-0 flex items-center justify-center">
            <div className="text-center text-white">
              <div className="w-20 h-20 mx-auto mb-4 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center border border-white/30">
                <svg className="w-8 h-8" fill="currentColor" viewBox="0 0 20 20">
                  <path d="M6.3 2.841A1.5 1.5 0 004 4.11V15.89a1.5 1.5 0 002.3 1.269l9.344-5.89a1.5 1.5 0 000-2.538L6.3 2.84z" />
                </svg>
              </div>
              <p className="text-lg font-medium">Video testimonial o B-roll inmersivo</p>
              <p className="text-sm text-white/70 mt-2">Experiencia NUMA en acción</p>
            </div>
          </div>
          {/* Uncomment when you have video
          <video
            autoPlay
            muted
            loop
            playsInLine
            className="w-full h-full object-cover"
          >
            <source src="/videos/testimonial.mp4" type="video/mp4" />
          </video>
          */}
        </motion.div>
      </div>
    </section>
  )
}
