'use client'

import { useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { ChevronLeft, ChevronRight, Maximize2 } from 'lucide-react'

const spaces = [
  {
    id: 1,
    title: 'Sauna Principal',
    subtitle: 'Infrared Heat Therapy',
    description: 'Sauna de infrarrojo de espectro completo con capacidad para 12 personas. Madera de cedro canadiense y piedras volcánicas mexicanas.',
    image: '/images/sauna.jpg',
    materials: 'Cedro, Piedra Volcánica, Vidrio Templado',
  },
  {
    id: 2,
    title: 'Ice Bath Chamber',
    subtitle: 'Cold Immersion',
    description: 'Piscina de inmersión fría 3-5°C con sistema de filtración ultravioleta. Iluminación circadiana ajustable.',
    image: '/images/ice-bath.jpg',
    materials: 'Acero Inoxidable, Piedra Caliza, LED RGB',
  },
  {
    id: 3,
    title: 'Breathwork Studio',
    subtitle: 'Guided Sessions',
    description: 'Estudio dedicado con sistema de sonido envolvente y ventilación controlada. Capacidad 20 personas.',
    image: '/images/breathwork.jpg',
    materials: 'Madera de Parota, Textiles Oaxaqueños, Cork',
  },
  {
    id: 4,
    title: 'Social Lounge',
    subtitle: 'Community Space',
    description: 'Espacio de convivencia con chimenea de bioetanol, biblioteca wellness y bar de adaptógenos.',
    image: '/images/lounge.jpg',
    materials: 'Roble Blanco, Lino Natural, Terracota',
  },
  {
    id: 5,
    title: 'Temazcal Moderno',
    subtitle: 'Ancestral Ritual',
    description: 'Temazcal contemporáneo que honra la tradición maya con tecnología moderna de vapor y aromaterapia.',
    image: '/images/temazcal.jpg',
    materials: 'Barro, Piedra Volcánica, Plantas Sagradas',
  },
]

export default function TheSpace() {
  const [currentIndex, setCurrentIndex] = useState(0)
  const [direction, setDirection] = useState(0)

  const slideVariants = {
    enter: (direction: number) => ({
      x: direction > 0 ? 1000 : -1000,
      opacity: 0,
    }),
    center: {
      zIndex: 1,
      x: 0,
      opacity: 1,
    },
    exit: (direction: number) => ({
      zIndex: 0,
      x: direction < 0 ? 1000 : -1000,
      opacity: 0,
    }),
  }

  const swipeConfidenceThreshold = 10000
  const swipePower = (offset: number, velocity: number) => {
    return Math.abs(offset) * velocity
  }

  const paginate = (newDirection: number) => {
    setDirection(newDirection)
    setCurrentIndex((prevIndex) => {
      let nextIndex = prevIndex + newDirection
      if (nextIndex < 0) nextIndex = spaces.length - 1
      if (nextIndex >= spaces.length) nextIndex = 0
      return nextIndex
    })
  }

  return (
    <section className="py-24 md:py-32 px-4 bg-white" id="space">
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true, margin: "-100px" }}
          transition={{ duration: 0.8 }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl md:text-5xl lg:text-6xl font-serif font-light text-aura-black mb-6">
            El{' '}
            <span className="text-aura-gold font-medium italic">espacio</span>
          </h2>
          <p className="text-xl md:text-2xl text-aura-black/70 max-w-3xl mx-auto">
            Arquitectura minimalista mexicana que honra la naturaleza
          </p>
        </motion.div>

        {/* Gallery Slider */}
        <div className="relative">
          {/* Main Image */}
          <div className="relative h-[500px] md:h-[700px] overflow-hidden rounded-3xl bg-gradient-to-br from-aura-obsidiana to-aura-jade">
            <AnimatePresence initial={false} custom={direction}>
              <motion.div
                key={currentIndex}
                custom={direction}
                variants={slideVariants}
                initial="enter"
                animate="center"
                exit="exit"
                transition={{
                  x: { type: "spring", stiffness: 300, damping: 30 },
                  opacity: { duration: 0.2 },
                }}
                drag="x"
                dragConstraints={{ left: 0, right: 0 }}
                dragElastic={1}
                onDragEnd={(e, { offset, velocity }) => {
                  const swipe = swipePower(offset.x, velocity.x)

                  if (swipe < -swipeConfidenceThreshold) {
                    paginate(1)
                  } else if (swipe > swipeConfidenceThreshold) {
                    paginate(-1)
                  }
                }}
                className="absolute inset-0"
              >
                {/* Image Placeholder */}
                <div className="absolute inset-0 bg-gradient-to-br from-aura-terracota/40 via-aura-jade/30 to-aura-gold/20" />

                {/* Content Overlay */}
                <div className="absolute inset-0 bg-gradient-to-t from-aura-black/80 via-aura-black/20 to-transparent flex items-end p-8 md:p-12">
                  <div className="max-w-2xl">
                    <motion.div
                      initial={{ opacity: 0, y: 20 }}
                      animate={{ opacity: 1, y: 0 }}
                      transition={{ delay: 0.2 }}
                    >
                      <p className="text-aura-gold text-sm md:text-base font-semibold mb-2 uppercase tracking-wider">
                        {spaces[currentIndex].subtitle}
                      </p>
                      <h3 className="text-3xl md:text-5xl font-serif font-light text-white mb-4">
                        {spaces[currentIndex].title}
                      </h3>
                      <p className="text-white/80 text-base md:text-lg mb-4 leading-relaxed">
                        {spaces[currentIndex].description}
                      </p>
                      <div className="flex items-center gap-2 text-sm text-white/60">
                        <span className="font-medium">Materiales:</span>
                        <span>{spaces[currentIndex].materials}</span>
                      </div>
                    </motion.div>
                  </div>
                </div>

                {/* Virtual Tour Badge */}
                <div className="absolute top-8 right-8">
                  <button className="glass px-4 py-2 rounded-full text-white text-sm font-medium flex items-center gap-2 hover:bg-white/20 transition-all">
                    <Maximize2 className="w-4 h-4" />
                    Tour Virtual 360°
                  </button>
                </div>
              </motion.div>
            </AnimatePresence>

            {/* Navigation Arrows */}
            <button
              onClick={() => paginate(-1)}
              className="absolute left-4 top-1/2 -translate-y-1/2 w-12 h-12 rounded-full bg-white/20 backdrop-blur-sm border border-white/30 flex items-center justify-center text-white hover:bg-white/30 transition-all z-10"
              aria-label="Previous image"
            >
              <ChevronLeft className="w-6 h-6" />
            </button>
            <button
              onClick={() => paginate(1)}
              className="absolute right-4 top-1/2 -translate-y-1/2 w-12 h-12 rounded-full bg-white/20 backdrop-blur-sm border border-white/30 flex items-center justify-center text-white hover:bg-white/30 transition-all z-10"
              aria-label="Next image"
            >
              <ChevronRight className="w-6 h-6" />
            </button>
          </div>

          {/* Thumbnails */}
          <div className="mt-6 grid grid-cols-5 gap-4">
            {spaces.map((space, index) => (
              <button
                key={space.id}
                onClick={() => {
                  setDirection(index > currentIndex ? 1 : -1)
                  setCurrentIndex(index)
                }}
                className={`relative h-20 md:h-24 rounded-lg overflow-hidden transition-all ${
                  index === currentIndex
                    ? 'ring-2 ring-aura-gold scale-105'
                    : 'opacity-60 hover:opacity-100'
                }`}
              >
                <div className={`absolute inset-0 bg-gradient-to-br ${
                  index % 3 === 0 ? 'from-aura-terracota/50 to-aura-gold/30' :
                  index % 3 === 1 ? 'from-aura-jade/50 to-aura-obsidiana/30' :
                  'from-aura-gold/50 to-aura-terracota/30'
                }`} />
                <div className="absolute inset-0 flex items-center justify-center">
                  <span className="text-white text-xs font-medium text-center px-2">
                    {space.title.split(' ')[0]}
                  </span>
                </div>
              </button>
            ))}
          </div>

          {/* Progress Indicators */}
          <div className="mt-6 flex justify-center gap-2">
            {spaces.map((_, index) => (
              <button
                key={index}
                onClick={() => {
                  setDirection(index > currentIndex ? 1 : -1)
                  setCurrentIndex(index)
                }}
                className={`h-1 rounded-full transition-all ${
                  index === currentIndex
                    ? 'w-12 bg-aura-gold'
                    : 'w-6 bg-aura-black/20 hover:bg-aura-black/40'
                }`}
                aria-label={`Go to slide ${index + 1}`}
              />
            ))}
          </div>
        </div>

        {/* Materials Info */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ delay: 0.4, duration: 0.6 }}
          className="mt-16 grid md:grid-cols-3 gap-8 text-center"
        >
          <div>
            <h4 className="text-2xl font-serif font-medium text-aura-black mb-2">
              Materiales Locales
            </h4>
            <p className="text-aura-black/70">
              Piedra volcánica, madera de parota y textiles oaxaqueños
            </p>
          </div>
          <div>
            <h4 className="text-2xl font-serif font-medium text-aura-black mb-2">
              Diseño Sostenible
            </h4>
            <p className="text-aura-black/70">
              Certificación LEED, energía solar y sistemas de reciclaje de agua
            </p>
          </div>
          <div>
            <h4 className="text-2xl font-serif font-medium text-aura-black mb-2">
              Arquitectura Mexicana
            </h4>
            <p className="text-aura-black/70">
              Minimalismo tropical que dialoga con el entorno natural
            </p>
          </div>
        </motion.div>
      </div>
    </section>
  )
}
